<script>
require('vue2-animate/dist/vue2-animate.min.css');

import TopNav from './components/ui/TopNav'
import PostalInput from './components/ui/PostalInput'
import Value from './components/ui/Value'
import FooterItem from './components/ui/Footer'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import {mapGetters, mapMutations} from 'vuex'
import { apolloClient } from './apollo'
import gql from 'graphql-tag'
export default {
  name: 'App',
  components: {
    Value,
    PostalInput,
    TopNav,
    FooterItem,
    swiper,
    swiperSlide
  },
  data() {
    return {
      swiperOptions: {  
        autoplay: {
          delay: 5000,
        },
          pagination: {
              el: '.swiper-pagination',
              clickable: true
          },
          navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev'
          }
      },
      locationIsGood: null,
      position: 'center',
      duration: 4000,
      isInfinity: true,
      emailRequest: false,
      email: '',
      error: null,
    }
  },
  methods: {
    emitLocation(data){
        this.locationIsGood = data;
        if(data === false){
          this.emailRequest = true;
        }
    },
    sendEmail(){
      let email = this.email;

      if(email !== ''){
          apolloClient.mutate({
            mutation: gql`
              mutation createUserEmail(
                $email: String!
              ){
                createUserEmail(data: {email: $email}){
                  id
                }
              }
            `,
            variables: {
              email,
            }
          }).then(data => {
            this.emailRequest = false;
          })
      }else{
          this.error = 'Ajoutez votre email et réessayez.'
      }
    },
    ...mapMutations([
      'kartSize'
    ])
  },
  computed: {
    ...mapGetters([
        'location'
    ]),
  },
  mounted(){
    console.log('mounted')
    this.kartSize()
  }
}
</script>

<style lang="scss">
