<script>
import CompleteMenu from './CompleteMenu'
import SliderShow from './SliderShow'

import {mapGetters, mapMutations} from 'vuex'
export default {  
    name: 'MenuSlider',
    components: {
        CompleteMenu,
        SliderShow
    },
    props:{
        currentMenuDay: {
            type: String,
            required: true,
        }
    },
    data() {
      return {
        veggieMode: false,
        payData: false,
        showKart: false,
        newClassicMenu: [],
        newVeggieMenu: [],
      }
    },
    computed: {
        ...mapGetters([
            'weekClassicMenu',
            'weekVeggieMenu',
            'weekDay',
            'myShopKart',
            'priceKart',
            'deliveryPrice',
            'location'
        ]),
    },
    methods: {
        ...mapMutations([
            'addMenu',
            'removeMenu',
            'setKartValueInState',
            'getCookie'
        ]),
        emitValue(data){
            this.$emit('currentMenuDay', data)
        },
        getCookie(name) {
            var value = "; " + document.cookie;
            var parts = value.split("; " + name + "=");
            if (parts.length == 2) {
                return parts.pop().split(";").shift();
            }else{
                return JSON.stringify({});
            }
        },
        getClassicMenu(){
            var newClassicMenu = [];
            this.weekClassicMenu.map(menu => {
                this.weekDay.map(day => {
                    if(menu.shortDate === day){
                        newClassicMenu.push(menu)
                    }
                })
            })
            this.newClassicMenu = newClassicMenu;
        },
        getVeggieMenu(){
            var newVeggieMenu = [];
            this.weekVeggieMenu.map(menu => {
                this.weekDay.map(day => {
                    if(menu.shortDate === day){
                        newVeggieMenu.push(menu)
                    }
                })
            })
            this.newVeggieMenu = newVeggieMenu;
        }
    },
    created(){
        this.$emit('md-changed');
        if(Object.keys(this.myShopKart).length === 0){
            let kartFromStorage = JSON.parse(this.getCookie("yumyKart")) || {};
            this.setKartValueInState(kartFromStorage)
        }
        this.getClassicMenu()
        this.getVeggieMenu()
    },
    updated(){
        let kartContent = JSON.stringify(this.myShopKart) || {};
        document.cookie = `yumyKart=${kartContent}; expires=31536e3, ${new Date()}`;
    }
}

</script>



